.Programs{
    margin: 80px auto;
    width: 90%;
    display: flex;
    
    align-items: center;
    justify-content: space-between;
}
.Program img{
    width:100% ;
    border-radius: 10px;
    display:block;
} 
.Program {
    flex-basis: 31%;
    position: relative;

}
.caption{
    border-radius: 10px;
    position: absolute;
    top:0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 15, 152, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff;
    cursor: pointer;
    opacity: 0;
    padding-top: 70%;
    transition: 0.4s;
}
.caption img{
    width: 60px;
    margin-bottom: 10px;
}
.Program:hover .caption{
opacity: 1;
padding-top: 0;
}

/* media query */
@media(max-width:650px){
    .Programs{
flex-direction: column;
    }
    .Program{
      margin: 20px;
        flex-basis: 100%;
    }
}