.hero {
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(rgba(8, 0, 58, 0.7), rgba(8, 0, 58, 0.7)), 
                url('../../Assets/images/hero.png');
    background-size: cover;
    background-position: center;
    color: #fff; /* Fix: Use #fff instead of #ffff */
    display: flex;
    align-items: center;
    justify-content: center;
}
   
.hero-text{
    text-align: center;
    max-width: 800px;
}
.btn2{
    background:#fff;
    color: #212121; 
    padding: 14px 25px;
    font-size: 16px;
    border-radius: 30px;
    cursor: pointer;
    border: 0;
    outline: 0;
}
.hero-text h1{
    font-size: 60px;
    font-weight: 600;
}
.hero-text p{
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4;
}

/* media query */
@media(max-width:850px){
    .hero-text h1{
        font-size: 40px;
    }
}
@media(max-width:650px){
    .hero-text h1{
        font-size: 30px;
        max-width: 400px;
        margin: auto;
    }
    .hero-text p{
        font-size: 14px;
        margin:15px auto 30px  ;
    }
}